.contact-section {
  padding: var(--space-xl) 0;
  background-color: var(--color-charcoal);
  min-height: 90vh;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.grain-overlay {
  width: 100%;
  height: 100%;
  opacity: .05;
  pointer-events: none;
  z-index: 0;
  mix-blend-mode: overlay;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.8' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  position: absolute;
  top: 0;
  left: 0;
}

.blur-glow {
  width: 70%;
  height: 70%;
  filter: blur(120px);
  z-index: 1;
  opacity: .6;
  background: radial-gradient(#c8553d26 0%, #c8553d0d 30%, #0000 70%);
  border-radius: 50%;
  animation: glow-pulse 12s ease-in-out infinite alternate;
  position: absolute;
  bottom: -15%;
  right: -10%;
}

@keyframes glow-pulse {
  0% {
    opacity: .4;
    transform: scale(1)translate(0);
  }

  100% {
    opacity: .7;
    transform: scale(1.1)translate(-30px, -20px);
  }
}

.circuit-pattern {
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: .15;
  background-image: linear-gradient(to right, #c8553d08 1px, #0000 1px), linear-gradient(#c8553d08 1px, #0000 1px);
  background-size: 50px 50px;
  position: absolute;
  top: 0;
  left: 0;
}

.circuit-pattern:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .5;
  background-image: radial-gradient(circle at 25% 25%, #c8553d0d 1px, #0000 1px), radial-gradient(circle at 75% 75%, #c8553d0d 1px, #0000 1px);
  background-size: 30px 30px;
  position: absolute;
  top: 0;
  left: 0;
}

.circuit-pattern:after {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .5;
  z-index: 1;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z' fill='%23c8553d' fill-opacity='0.02' fill-rule='evenodd'/%3E%3C/svg%3E");
  position: absolute;
  top: 0;
  left: 0;
}

.contact-section:before {
  content: "";
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, transparent, var(--color-terracotta-dark), transparent);
  opacity: .6;
  position: absolute;
  top: 0;
  left: 0;
}

.contact-container {
  gap: var(--space-lg);
  z-index: 5;
  perspective: 1000px;
  grid-template-columns: 1fr 1.5fr;
  display: grid;
  position: relative;
}

.contact-info {
  opacity: 0;
  transform-style: preserve-3d;
  transition: opacity 1s, transform 1s;
  transform: translateX(-30px)perspective(1000px);
}

.contact-info.animate-fade-in-left {
  opacity: 1;
  transform: translateX(0)perspective(1000px);
}

.contact-card {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-left: 4px solid var(--color-terracotta);
  padding: var(--space-lg);
  height: 100%;
  transform-style: preserve-3d;
  background-color: #1a1a1ab3;
  background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23f5f2e9' fill-opacity='0.02' fill-rule='evenodd'%3E%3Cpath d='M0 0h40v40H0V0zm40 40h40v40H40V40zm0-40h2l-2 2V0zm0 4l4-4h2L40 6V4zm0 4l8-8h2L40 10V8zm0 4L52 0h2L40 14v-2zm0 4L56 0h2L40 18v-2zm0 4L60 0h2L40 22v-2zm0 4L64 0h2L40 26v-2zm0 4L68 0h2L40 30v-2zm0 4L72 0h2L40 34v-2zm0 4L76 0h2L40 38v-2zm0 4L80 0v2L42 40h-2zm4 0L80 4v2L46 40h-2zm4 0L80 8v2L50 40h-2zm4 0l28-28v2L54 40h-2zm4 0l24-24v2L58 40h-2zm4 0l20-20v2L62 40h-2zm4 0l16-16v2L66 40h-2zm4 0l12-12v2L70 40h-2zm4 0l8-8v2l-6 6h-2zm4 0l4-4v2l-2 2h-2z'/%3E%3C/g%3E%3C/svg%3E");
  border-radius: 2px;
  flex-direction: column;
  justify-content: space-between;
  transition: all .5s cubic-bezier(.16, 1, .3, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 15px 35px #0006, 0 0 0 1px #c8553d1a, inset 0 0 15px #0003;
}

.contact-card:hover {
  transform: translateY(-5px)rotateX(2deg);
  box-shadow: 0 20px 40px #00000080, 0 0 0 1px #c8553d33, inset 0 0 15px #0003;
}

.card-glow {
  width: 200%;
  height: 200%;
  opacity: 0;
  pointer-events: none;
  z-index: -1;
  background: radial-gradient(circle, #c8553d12 0%, #0000 50%);
  transition: opacity .5s;
  position: absolute;
  top: -50%;
  left: -50%;
}

.contact-card:hover .card-glow {
  opacity: 1;
}

.contact-card:before {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, transparent 97%, var(--color-terracotta-dark) 97%);
  opacity: .2;
  position: absolute;
  top: 0;
  left: 0;
}

.contact-card:after {
  content: "";
  width: 40%;
  height: 1px;
  background: linear-gradient(to left, var(--color-terracotta-dark), transparent);
  opacity: .3;
  position: absolute;
  bottom: 0;
  right: 0;
}

.contact-card-title {
  color: var(--color-parchment);
  margin-bottom: var(--space-md);
  z-index: 1;
  font-size: 1.8rem;
  font-weight: 700;
  font-family: var(--font-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  transform: translateZ(10px);
}

.contact-card-title:after {
  content: "";
  width: 40px;
  height: 2px;
  background-color: var(--color-terracotta);
  transition: width .3s;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.contact-card:hover .contact-card-title:after {
  width: 60px;
}

.contact-methods {
  gap: var(--space-md);
  flex-direction: column;
  display: flex;
  transform: translateZ(5px);
}

.contact-method {
  transform-style: preserve-3d;
  align-items: flex-start;
  transition: all .3s;
  display: flex;
}

.contact-method:hover {
  transform: translateX(5px)translateZ(10px);
}

.contact-icon {
  width: 50px;
  height: 50px;
  margin-right: var(--space-sm);
  color: var(--color-terracotta);
  background-color: #1a1a1acc;
  border: 1px solid #c8553d4d;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  transition: all .3s cubic-bezier(.16, 1, .3, 1);
  display: flex;
  transform: translateZ(15px);
  box-shadow: 0 4px 8px #0003;
}

.contact-method:hover .contact-icon {
  background-color: var(--color-terracotta);
  color: var(--color-parchment);
  transform: translateY(-3px)translateZ(20px)rotateY(10deg);
  box-shadow: 0 8px 15px #c8553d4d;
}

.contact-text {
  transform: translateZ(5px);
}

.contact-text h4 {
  color: var(--color-parchment);
  font-size: 1rem;
  font-family: var(--font-secondary);
  letter-spacing: .5px;
  margin-bottom: 5px;
}

.contact-text p, .contact-text a {
  color: var(--color-stone);
  font-size: .95rem;
  transition: color .3s;
}

.contact-link {
  text-decoration: none;
  position: relative;
}

.contact-link:after {
  content: "";
  width: 0;
  height: 1px;
  background-color: var(--color-terracotta);
  transition: width .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.contact-link:hover {
  color: var(--color-terracotta);
}

.contact-link:hover:after {
  width: 100%;
}

.social-links {
  gap: var(--space-sm);
  margin-top: var(--space-lg);
  display: flex;
  transform: translateZ(10px);
}

.social-link {
  width: 45px;
  height: 45px;
  color: var(--color-parchment);
  transform-style: preserve-3d;
  perspective: 500px;
  background-color: #1a1a1acc;
  border: 1px solid #c8553d1a;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
  display: flex;
}

.social-link:hover {
  background-color: var(--color-terracotta);
  color: var(--color-parchment);
  transform: translateY(-5px)rotateX(10deg)rotateY(10deg);
  box-shadow: 0 8px 15px #0003, 0 0 0 1px #c8553d4d;
}

.contact-form-container {
  opacity: 0;
  transform-style: preserve-3d;
  perspective: 1000px;
  transition: opacity 1s, transform 1s;
  transform: translateX(30px)perspective(1000px);
}

.contact-form-container.animate-fade-in-right {
  opacity: 1;
  transform: translateX(0)perspective(1000px);
}

.contact-form {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  transform-style: preserve-3d;
  background-color: #1a1a1ab3;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23c8553d' fill-opacity='0.03' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
  border-radius: 2px;
  overflow: hidden;
  box-shadow: 0 15px 35px #0000004d, 0 0 0 1px #c8553d0d, inset 0 0 15px #0003;
}

.form-header {
  padding: var(--space-md);
  border-bottom: 2px solid var(--color-terracotta);
  background-color: #141414cc;
  position: relative;
  overflow: hidden;
  transform: translateZ(5px);
}

.form-header:before {
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, #0000, #c8553d1a, #0000);
  animation: shimmer 3s infinite;
  position: absolute;
  top: 0;
  left: -100%;
  transform: skewX(-25deg);
}

@keyframes shimmer {
  0% {
    left: -100%;
  }

  100% {
    left: 200%;
  }
}

.form-header h3 {
  color: var(--color-parchment);
  z-index: 1;
  font-size: 1.5rem;
  font-family: var(--font-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0;
  position: relative;
}

.form-row {
  gap: var(--space-md);
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.form-group {
  padding: var(--space-md);
  transition: all .3s;
  position: relative;
  transform: translateZ(0);
}

.form-group.focused {
  transform: translateZ(5px);
}

.form-group label {
  color: var(--color-stone);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-size: .9rem;
  font-weight: 500;
  font-family: var(--font-secondary);
  margin-bottom: 8px;
  transition: all .3s;
  display: block;
}

.form-group.focused label {
  color: var(--color-terracotta-light);
}

.input-wrapper {
  perspective: 1000px;
  position: relative;
}

.focus-border {
  width: 0;
  height: 2px;
  background: linear-gradient(to right, var(--color-terracotta-dark), var(--color-terracotta), var(--color-terracotta-light));
  z-index: 1;
  transition: width .3s cubic-bezier(.16, 1, .3, 1);
  position: absolute;
  bottom: 0;
  left: 0;
}

.form-group.focused .focus-border {
  width: 100%;
  animation: border-pulse 2s infinite alternate;
}

@keyframes border-pulse {
  0% {
    opacity: .7;
  }

  100% {
    opacity: 1;
  }
}

.form-group input, .form-group textarea {
  width: 100%;
  color: var(--color-parchment);
  font-size: 1rem;
  font-family: var(--font-primary);
  background-color: #0f0f0fb3;
  border: 1px solid #c8553d1a;
  border-radius: 2px;
  padding: 12px 15px;
  transition: all .3s cubic-bezier(.16, 1, .3, 1);
  transform: translateZ(0);
  box-shadow: inset 0 2px 4px #0003;
}

.form-group.focused input, .form-group.focused textarea {
  background-color: #141414e6;
  border-color: #c8553d4d;
  transform: translateZ(2px);
  box-shadow: 0 0 0 1px #c8553d1a, inset 0 2px 4px #0000004d;
}

.form-group input:focus, .form-group textarea:focus {
  outline: none;
}

.form-group input::placeholder, .form-group textarea::placeholder {
  color: var(--color-gray-dark);
  opacity: .7;
}

.textarea-wrapper {
  height: 100%;
}

.form-group textarea {
  resize: none;
  min-height: 150px;
}

.submit-btn {
  width: calc(100% - 2 * var(--space-md));
  margin: var(--space-md);
  background-color: var(--color-terracotta);
  color: var(--color-parchment);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  cursor: pointer;
  transform-style: preserve-3d;
  font-size: 1.1rem;
  font-weight: 600;
  font-family: var(--font-secondary);
  border: none;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 15px 25px;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  transform: translateZ(0);
  box-shadow: 0 4px 6px #0003;
}

.submit-btn:hover {
  background-color: var(--color-terracotta-dark);
  transform: translateY(-5px)translateZ(10px);
  box-shadow: 0 15px 25px #0000004d;
}

.submit-btn:before {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #0000, #f5f2e933, #0000);
  transition: all .6s;
  position: absolute;
  top: 0;
  left: -100%;
}

.submit-btn:hover:before {
  left: 100%;
}

.btn-text {
  z-index: 1;
  position: relative;
}

.btn-icon {
  z-index: 1;
  transition: transform .3s;
  display: inline-flex;
}

.submit-btn:hover .btn-icon {
  transform: rotate(-25deg)translateX(3px);
}

.form-success {
  text-align: center;
  height: 100%;
  padding: var(--space-lg);
  transform-style: preserve-3d;
  background-color: #1a1a1ab3;
  background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23f5f2e9' fill-opacity='0.02' fill-rule='evenodd'%3E%3Cpath d='M0 0h40v40H0V0zm40 40h40v40H40V40zm0-40h2l-2 2V0zm0 4l4-4h2L40 6V4zm0 4l8-8h2L40 10V8zm0 4L52 0h2L40 14v-2zm0 4L56 0h2L40 18v-2zm0 4L60 0h2L40 22v-2zm0 4L64 0h2L40 26v-2zm0 4L68 0h2L40 30v-2zm0 4L72 0h2L40 34v-2zm0 4L76 0h2L40 38v-2zm0 4L80 0v2L42 40h-2zm4 0L80 4v2L46 40h-2zm4 0L80 8v2L50 40h-2zm4 0l28-28v2L54 40h-2zm4 0l24-24v2L58 40h-2zm4 0l20-20v2L62 40h-2zm4 0l16-16v2L66 40h-2zm4 0l12-12v2L70 40h-2zm4 0l8-8v2l-6 6h-2zm4 0l4-4v2l-2 2h-2z'/%3E%3C/g%3E%3C/svg%3E");
  border-radius: 2px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.success-icon {
  color: var(--color-terracotta);
  width: 80px;
  height: 80px;
  margin-bottom: var(--space-md);
  z-index: 1;
  opacity: 0;
  animation: success-appear .5s forwards;
  position: relative;
  transform: scale(.5);
}

@keyframes success-appear {
  0% {
    opacity: 0;
    transform: scale(.5);
  }

  70% {
    opacity: 1;
    transform: scale(1.1);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.form-success h3 {
  color: var(--color-parchment);
  margin-bottom: var(--space-sm);
  z-index: 1;
  opacity: 0;
  font-size: 2rem;
  font-family: var(--font-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  animation: fade-in-up .5s .2s forwards;
  position: relative;
  transform: translateY(20px);
}

.form-success p {
  color: var(--color-stone);
  z-index: 1;
  opacity: 0;
  font-size: 1.1rem;
  animation: fade-in-up .5s .4s forwards;
  position: relative;
  transform: translateY(20px);
}

@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.success-glow {
  width: 200px;
  height: 200px;
  z-index: 0;
  background: radial-gradient(circle, #c8553d33 0%, #0000 70%);
  border-radius: 50%;
  animation: success-glow 3s infinite alternate;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes success-glow {
  0% {
    opacity: .5;
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    opacity: .8;
    transform: translate(-50%, -50%)scale(1.2);
  }
}

@media (prefers-reduced-motion: reduce) {
  .blur-glow, .form-header:before, .submit-btn:before, .success-glow {
    animation: none;
  }

  .success-icon, .form-success h3, .form-success p {
    opacity: 1;
    animation: none;
    transform: none;
  }

  .submit-btn:hover, .contact-method:hover .contact-icon, .social-link:hover {
    transform: none;
  }
}

@media (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr;
  }

  .contact-card {
    margin-bottom: var(--space-md);
  }
}

@media (max-width: 768px) {
  .form-row {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .social-links {
    justify-content: center;
  }
}

@media (max-width: 576px) {
  .contact-methods {
    gap: var(--space-sm);
  }

  .contact-method {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }

  .contact-icon {
    margin-right: 0;
    margin-bottom: var(--space-xs);
  }
}

.modal-overlay {
  backdrop-filter: blur(5px);
  z-index: 1000;
  background-color: #141414d9;
  justify-content: center;
  align-items: flex-start;
  padding: 60px 20px;
  animation: fade-in .3s ease-out;
  display: flex;
  position: fixed;
  inset: 0;
  overflow-y: auto;
}

.modal-container {
  width: 100%;
  max-width: 900px;
  perspective: 1200px;
}

.modal-content {
  background: var(--color-charcoal);
  transform-origin: top;
  max-height: calc(100vh - 120px);
  scrollbar-width: thin;
  scrollbar-color: var(--color-terracotta) var(--color-black-light);
  border: 1px solid #c8553d4d;
  border-radius: 6px;
  animation: modal-in .5s cubic-bezier(.16, 1, .3, 1);
  position: relative;
  overflow-y: auto;
  box-shadow: 0 25px 50px -12px #000000b3, 0 0 0 1px #c8553d1a, 0 0 30px #c8553d1a;
}

